import { useState, useEffect } from 'react';
import { useLocation } from '@reach/router';

export const useActiveMenu = (title: string) => {
  const { pathname } = useLocation();
  const [isActive, setIsActive] = useState<boolean>(false);

  useEffect(() => {
    if (
      (pathname === '/' || pathname === '/th' || pathname === '/th/') &&
      title === ''
    ) {
      setIsActive(true);
    } else if (pathname.includes('/contact') && title === '') {
      setIsActive(true);
    } else if (pathname.includes('/news') && title === '') {
      setIsActive(true);
    } else if (pathname.includes('/about') && title === '') {
      setIsActive(true);
    } else {
      setIsActive(false);
    }
  }, [pathname, title]);

  return isActive;
};
